/* About page */

.About {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: wrap;
          flex-flow: wrap;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  padding: 50px 15%;
}

.About-img {
  padding: 10px;
}

.About-img > img {
  height: auto;
  width: 400px;
}

.About-text {
  padding: 0px 50px 50px 50px;
  min-width: 350px;
  /* text-align: left; */
  font-size: 20px;
  font-family: "Benne", serif;
}

.About-text > p:first-child {
  font-size: 55px;
  margin: 0px;
}

@media only screen and (max-width: 450px) {
  .About-img > img {
    width: 200px;
  }

  .About {
    padding: 50px 0;
  }

  .About-text {
    padding: 0px 25px;
  }
}

/* Gallery */

.Gallery {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: wrap;
          flex-flow: wrap;
  -webkit-align-items: center;
          align-items: center;
  /* padding: 50px 15%; */
  height: 100%;
  -webkit-justify-content: center;
          justify-content: center;
}

.Gallery-Tile {
  height: auto;
  width: 500px;
  padding: 100px;
}

@media only screen and (max-width: 615px) {
  .Gallery-Tile {
    width: 250px;
    padding: 25px;
  }
}

/* Photography */

.Photography {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: wrap;
          flex-flow: wrap;
  font-family: "Benne", serif;
  -webkit-justify-content: center;
          justify-content: center;
  /* text-align: left;*/
  font-size: 20px;
  padding: 50px 0;
}

.Photography-Wrapper {
  width: 50%;
  min-width: 300px;
}

.Photography-Wrapper > p {
  width: 100%;
}

.Photography-Wrapper > p:first-child {
  font-size: 55px;
  margin-top: 0px;
}

.Photography-Pricing {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: wrap;
          flex-flow: wrap;
  width: 100%;
  -webkit-justify-content: center;
          justify-content: center;
}

.Photography-Pricing > p {
  padding-right: 20px;
}

@media only screen and (max-width: 450px) {
}

/* Prints */

.Prints {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: wrap;
          flex-flow: wrap;
  -webkit-justify-content: center;
          justify-content: center;
  font-family: "Benne", serif;
  font-size: 20px;
}

.Prints-Display {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: wrap;
          flex-flow: wrap;
  -webkit-align-items: center;
          align-items: center;
  height: 100%;
  -webkit-justify-content: center;
          justify-content: center;
}

.Print-Tile {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: wrap;
          flex-flow: wrap;
  -webkit-justify-content: center;
          justify-content: center;
}

.Print-Tile-Img:first-child {
  padding: 50px 0 20px;
}

.Print-Tile-Img {
  height: auto;
  width: 500px;
  padding: 100px 0 20px;
}

.Print-Tile > p {
  padding: 0;
  margin: 0;
  width: 100%;
}

@media only screen and (max-width: 615px) {
  .Print-Tile-Img {
    width: 250px;
    padding: 25px;
  }
  .Prints > p:first-child {
    width: 80%;
  }
}

